import IArchetype from "./Archetype";
import { Attributes, SkillDistribution, Skills } from "./Constants";
import Player from "./Character";

export default function choose(options: Array<any>): any {
    if (options[0] instanceof Array) {
        return options.map((option: any) => choose(option));
    }
    return options[Math.floor(Math.random() * options.length)];
}

export function distributeAttributes(
    points: number,
    main_attribute: string
): Attributes {
    let attributes: Attributes = {
        physique: 0,
        precision: 0,
        logic: 0,
        empathy: 0,
    };
    Object.keys(attributes).forEach((key) => {
        attributes[key] = 2;
        points = points - 2;
    });
    attributes[main_attribute] += 3;
    points = points - 3;
    while (points > 0) {
        var attribute = choose(Object.keys(attributes));
        if (attribute !== main_attribute) {
            attributes[attribute]++;
            points--;
        }
    }
    return attributes;
}

export function distributeSkills(
    points: number,
    main_skill: string,
    attributes: Attributes
) {
    let skills: Skills = {
        physique: {
            agility: 0,
            close_combat: 0,
            force: 0,
        },
        precision: {
            medicine: 0,
            ranged_combat: 0,
            stealth: 0,
        },
        logic: {
            investigation: 0,
            learning: 0,
            vigilance: 0,
        },
        empathy: {
            inspiration: 0,
            manipulation: 0,
            observation: 0,
        },
    };

    let distribution: Record<string, number> = {};
    Object.keys(attributes).forEach((attribute) => {
        distribution[attribute] = attributes[attribute] / points;
    });

    Object.keys(attributes).forEach((attribute) => {
        Object.keys(skills[attribute]).forEach((skill) => {
            if (skill === main_skill) {
                skills[attribute][skill] += 3;
                points -= 3;
            }
        });
    });

    while (points > 0) {
        let skill_type: string = weighted_random(
            Object.keys(skills),
            Object.values(distribution)
        );
        let skill = choose(Object.keys(skills[skill_type]));

        if (skills[skill_type][skill] < 2) {
            skills[skill_type][skill] += 1;
            points -= 1;
        }
    }

    return skills;
}

function weighted_random<T>(items: Array<T>, weights: Array<number>): T {
    var i;

    for (i = 0; i < weights.length; i++) weights[i] += weights[i - 1] || 0;

    var random = Math.random() * weights[weights.length - 1];

    for (i = 0; i < weights.length; i++) if (weights[i] > random) break;

    return items[i];
}

export function generate(archetype: IArchetype): Player {
    let player: Player = {
        name: choose(archetype.name),
        age: choose(archetype.age),
        archetype: archetype.archetype,
        motivation: choose(archetype.motivation),
        trauma: choose(archetype.trauma),
        dark_secret: choose(archetype.dark_secret),
        relationships: choose(archetype.relationships),
        talent: choose(archetype.talents),
        equipment: choose(archetype.equipment),
        memento: "",
        attributes: {
            physique: 0,
            precision: 0,
            logic: 0,
            empathy: 0,
        },
        skills: {
            physique: {
                agility: 0,
                close_combat: 0,
                force: 0,
            },
            precision: {
                medicine: 0,
                ranged_combat: 0,
                stealth: 0,
            },
            logic: {
                investigation: 0,
                learning: 0,
                vigilance: 0,
            },
            empathy: {
                inspiration: 0,
                manipulation: 0,
                observation: 0,
            },
        },
        resources: choose(["Middle Class", "Comfortable", "Well-off"]),
    };

    let attributes = SkillDistribution[player.age].attributes;
    player.attributes = distributeAttributes(
        attributes,
        archetype.main_attribute
    );
    let skills = SkillDistribution[player.age].skills;
    player.skills = distributeSkills(
        skills,
        archetype.main_skill,
        player.attributes
    );
    return player;
}
