import React, { useState } from "react";
import { Archetypes } from "../model/Archetype";
import choose, { generate } from "../model/Generator";
import Player from "../model/Character";
import CharacterSheet from "./CharacterSheet";
import styled from "styled-components";

export default function Generator() {
    const [player, setPlayer] = useState<Player>();

    function generatePlayer() {
        setPlayer(generate(choose(Archetypes)));
    }

    return (
        <Content>
            {player ? <CharacterSheet player={player}></CharacterSheet> : null}
            <GenerateButton onClick={generatePlayer}>
                {player ? "Try that again..." : "Generate a Vaesen Hunter"}
            </GenerateButton>
        </Content>
    );
}

const Content = styled.div`
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

const GenerateButton = styled.button`
    height: 130px;
    width: 350px;
    font-size: 24px;
    margin-top: 16px;
    margin-bottom: 16px;
    background-color: #507f8d;
    color: #f7f6f4;
    outline: none;
    border: none;

    @media (max-width: 800px) {
        height: 100px;
        width: 200px;
        font-size: 20px;
    }
`;
