import React from "react";
import Character from "../model/Character";
import styled from "styled-components";
import { isPartiallyEmittedExpression } from "typescript";
import Section from "./Section";
import Field from "./Field";
import Item, { ItemSpan } from "./Item";
import NumberField from "./NumberField";

interface PlayerCardProps {
    player: Character;
}

export default function CharacterSheet(props: PlayerCardProps) {
    var skills = Object.values(props.player.skills)
        .map((value) => Object.keys(value))
        .flat(2);
    console.log(skills);
    // var skills = Object.keys(props.player.attributes).map(attribute => Object.values(props.player.attributes[attribute]));

    return (
        <Sheet>
            <Right>
                <Section title="CHARACTER">
                    <Field title="NAME">{props.player.name.join(" ")}</Field>
                    <ItemSpan>
                        <Field title="AGE/AGE GROUP" className={"aged"}>
                            {props.player.age}
                        </Field>
                        <Field className="grow" title={"ARCHETYPE"}>
                            {props.player.archetype}
                        </Field>
                    </ItemSpan>
                    <Field title="MOTIVATION">{props.player.motivation}</Field>
                    <Field title="TRAUMA">{props.player.trauma}</Field>
                    <Field title="DARK SECRET">
                        {props.player.dark_secret}
                    </Field>
                </Section>

                <Section title="RELATIONSHIPS">
                    {Object.values(props.player.relationships).map(
                        (relationship, index) => {
                            return (
                                <Item>
                                    PC{index + 1}: {relationship}
                                </Item>
                            );
                        }
                    )}
                </Section>

                <Section title="TALENTS">
                    <Item>{props.player.talent}</Item>
                </Section>

                <Section title="EQUIPMENT">
                    {Object.values(props.player.equipment).map((equip) => {
                        return <Item>{equip}</Item>;
                    })}
                </Section>
            </Right>
            <Left>
                <Section title={"ATTRIBUTES"}>
                    {Object.keys(props.player.attributes).map((attribute) => {
                        return (
                            <NumberField
                                title={
                                    attribute[0].toUpperCase() +
                                    attribute.slice(1)
                                }
                                value={props.player.attributes[attribute]}
                                bgColor={
                                    Object.keys(
                                        props.player.attributes
                                    ).indexOf(attribute) %
                                        2 ===
                                    0
                                        ? "#f7f6f4"
                                        : ""
                                }
                            />
                        );
                    })}
                </Section>

                <Section title="SKILLS">
                    {Object.keys(props.player.skills).map((attribute) => {
                        return Object.keys(props.player.skills[attribute]).map(
                            (skill) => {
                                return (
                                    <NumberField
                                        title={
                                            skill[0].toUpperCase() +
                                            skill.slice(1).replace("_", " ") +
                                            " (" +
                                            attribute[0].toUpperCase() +
                                            attribute.slice(1) +
                                            ")"
                                        }
                                        value={
                                            props.player.skills[attribute][
                                                skill
                                            ]
                                        }
                                        bgColor={
                                            skills.indexOf(skill) % 2 === 0
                                                ? "#f7f6f4"
                                                : ""
                                        }
                                    />
                                );
                            }
                        );
                    })}
                </Section>
            </Left>
        </Sheet>
    );
}

const Sheet = styled.div`
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    gap: 8px;
    margin-top: 4px;

    @media (max-width: 900px) {
        width: 98%;
    }
`;

const Right = styled.div`
    display: flex;
    flex-direction: column;
    width: 70%;
    border: 1px solid #b19d8d;
    padding: 8px;

    .aged {
        min-width: 125px;
    }

    @media (max-width: 900px) {
        width: 50%;
    }
`;

const Left = styled.div`
    flex-grow: 1;
    border: 1px solid #b19d8d;
    padding: 8px;
`;
