export enum AgeGroup {
    YOUNG = "Young",
    MIDDLE_AGED = "Middle Aged",
    OLD = "Old",
}

type IDistribution = {
    [key in AgeGroup]: { skills: number; attributes: number };
};

export const SkillDistribution: IDistribution = {
    Young: { skills: 10, attributes: 15 },
    "Middle Aged": { skills: 12, attributes: 14 },
    Old: { skills: 14, attributes: 13 },
};

export type AttributeName = "physique" | "precision" | "logic" | "empathy";
export type SkillName =
    | "agility"
    | "close_combat"
    | "force"
    | "medicine"
    | "ranged_combat"
    | "stealth"
    | "investigation"
    | "learning"
    | "vigiliance"
    | "inspiration"
    | "manipulation"
    | "observation";

export type Attributes = {
    [key: string]: number;
    physique: number;
    precision: number;
    logic: number;
    empathy: number;
};

export type Skills = {
    [key: string]: { [key: string]: number };
    physique: {
        agility: number;
        close_combat: number;
        force: number;
    };
    precision: {
        medicine: number;
        ranged_combat: number;
        stealth: number;
    };
    logic: {
        investigation: number;
        learning: number;
        vigilance: number;
    };
    empathy: {
        inspiration: number;
        manipulation: number;
        observation: number;
    };
};

